@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
}

.app {
  min-height: 100vh;
  width: 100%;
}

.youtube-ad{border-radius:15px;position:relative}.youtube-ad .image{width:100%;height:280px}.youtube-ad .image img{width:100%;height:100%;border-radius:15px;box-shadow:0 0 20px 0 rgba(0,0,0,0.22)}.youtube-ad .about-ad{position:absolute;top:30px;left:20px;height:90px;display:flex;flex-direction:column;justify-content:space-around}.youtube-ad .about-ad .upper{width:400px}.youtube-ad .about-ad .upper img{width:100%}.youtube-ad .about-ad .lower{font-size:18px}.youtube-ad .one-month-free-btn{position:absolute;background:transparent;padding:8px 20px;outline:none;border:2px solid blue;font-size:14px;color:#1e6dff;bottom:50px;font-weight:600;left:20px;text-transform:uppercase;cursor:pointer}@media screen and (max-width: 560px){.youtube-ad .about-ad .upper img{-webkit-backdrop-filter:blur(1px);backdrop-filter:blur(1px)}.youtube-ad .about-ad .lower{-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);font-size:18px}.youtube-ad .one-month-free-btn{padding:7px 10px;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);color:#0004ff}}

.header{width:100%;background-color:#fff;height:65px;border-radius:20px;box-shadow:0 0 20px 0 rgba(0,0,0,0.1);display:flex;align-items:center;padding:0px 20px;justify-content:space-between}.header.dark-mode{background:#1f1e1e}.header.dark-mode .options .option{background-color:rgba(71,67,67,0.49)}.header.dark-mode .options .option .MuiSvgIcon-root{color:white}.header.dark-mode .options .active{background:rgba(240,95,95,0.1)}.header.dark-mode .options .active .MuiSvgIcon-root{color:red}.header.dark-mode .search-field input{background:black;color:white}.header.dark-mode .search-field input::-webkit-input-placeholder{color:rgba(255,255,255,0.589)}.header.dark-mode .search-field input:-ms-input-placeholder{color:rgba(255,255,255,0.589)}.header.dark-mode .search-field input::placeholder{color:rgba(255,255,255,0.589)}.header .options{width:25%;display:flex;justify-content:space-around}.header .options .option{width:45px;background-color:rgba(0,0,0,0.04);height:33px;display:flex;align-items:center;justify-content:center;border-radius:8px;box-shadow:0 0 20px 0 rgba(0,0,0,0.01)}.header .options .option .MuiSvgIcon-root{color:rgba(0,0,0,0.6);font-size:21px}.header .options .active{background:rgba(255,0,0,0.1)}.header .options .active .MuiSvgIcon-root{color:red}.header .search-field{width:35%}.header .search-field input{width:100%;background-color:rgba(0,0,0,0.055);outline:none;border:0;padding:12px 18px;box-shadow:0 0 20px 0 rgba(0,0,0,0.05);border-radius:10px}.header .search-field input::-webkit-input-placeholder{color:rgba(0,0,0,0.4)}.header .search-field input:-ms-input-placeholder{color:rgba(0,0,0,0.4)}.header .search-field input::placeholder{color:rgba(0,0,0,0.4)}@media screen and (max-width: 560px){.header{height:120px;margin:25px 0;display:grid;grid-template-columns:50% 50%;grid-template-rows:50% 50%;grid-template-areas:"search search" "options-one options-two";place-items:center}.header .options .option{width:30px}.header .options .option .MuiSvgIcon-root{font-size:19px}.header .first-options-set{grid-area:options-one;width:100%}.header .search-field{grid-area:search;width:100%;margin-top:10px}.header .second-options-set{grid-area:options-two;width:100%}}

.home-page-videos-container.dark-mode{background:black}.home-page-videos-container.dark-mode .heading{color:white}.home-page-videos-container .heading{font-size:25px;text-align:left;width:100%;margin:25px 0;font-weight:400}.home-page-videos-container .home-page-videos{display:grid;grid-template-columns:repeat(auto-fill, minmax(210px, 1fr));grid-gap:3rem 1rem;gap:3rem 1rem;margin-top:30px;place-items:center}@media screen and (max-width: 560px){.home-page-videos-container .heading{text-align:center}.home-page-videos-container .home-page-videos{grid-gap:2rem 4rem;gap:2rem 4rem}}

.video-card{height:330px;background:white;padding:20px 12px;box-shadow:0 0 20px 0 rgba(0,0,0,0.2);border-radius:20px;min-width:250px;max-width:250px}.video-card.dark-mode{background:#1a1919}.video-card.dark-mode .title{color:white}.video-card.dark-mode .channel-name,.video-card.dark-mode .lower span{color:rgba(255,255,255,0.342)}.video-card .thumbnail{width:100%;position:relative}.video-card .thumbnail img{max-width:100%;height:170px;border-radius:12px;box-shadow:0 0 20px 0 rgba(0,0,0,0.35);border:2px solid white}.video-card .thumbnail .channel-pic{position:absolute;bottom:-10px;left:9px;background-color:#fff;box-shadow:0 0 20px 0 rgba(0,0,0,0.2);border-radius:15px;height:40px;width:39px;padding:2px}.video-card .thumbnail .channel-pic img{height:37px;width:37px}.video-card .title,.video-card .channel-name,.video-card .lower{padding:4px 12px}.video-card .title{font-size:14px;font-weight:bold;padding-top:22px;line-height:22px;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden;text-overflow:ellipsis}.video-card .channel-name{color:rgba(0,0,0,0.664);display:flex;align-items:center;width:100%}.video-card .channel-name span{font-size:14px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.video-card .channel-name .MuiSvgIcon-root{font-size:14px;margin-left:10px}.video-card .lower span{color:rgba(0,0,0,0.664);font-size:13px;margin:0 3px}@media screen and (max-width: 560px){.video-card{width:310px;padding:22px 20px}}

.trending-interests{width:100%;margin:20px 0}.trending-interests.dark-mode .interests{background:black}.trending-interests.dark-mode .interests .topics{background-color:rgba(36,34,34,0.68);color:white;box-shadow:0 0 20px 0 rgba(255,255,255,0.05)}.trending-interests.dark-mode .interests .active{background:white;color:black}.trending-interests.dark-mode .interests .see-more{background-color:rgba(36,34,34,0.68);color:white;cursor:pointer}.trending-interests.dark-mode .interests .see-more::before{background:linear-gradient(to right, rgba(15,15,15,0.12) 30%, rgba(14,11,11,0.99) 75%)}.trending-interests .interests{display:flex;justify-content:space-around;width:100%}.trending-interests .interests .topics{text-decoration:none;color:black;background-color:rgba(255,255,255,0.6);box-shadow:0 0 20px 0 rgba(0,0,0,0.1);padding:8px 20px;border-radius:7px;font-size:14px}.trending-interests .interests .active{background:rgba(0,0,0,0.788);color:white}.trending-interests .interests .see-more{border-radius:7px;background-color:rgba(255,255,255,0.6);box-shadow:0 0 20px 0 rgba(0,0,0,0.1);display:flex;align-items:center;position:relative}.trending-interests .interests .see-more::before{position:absolute;content:"";width:120px;height:32px;border-radius:7px;background:linear-gradient(to right, rgba(245,245,245,0.6) 10%, #f5f5f5 90%);left:-125px}@media screen and (max-width: 560px){.trending-interests{position:relative}.trending-interests .interests{overflow:scroll;overflow-y:hidden;padding:30px 20px;margin:auto;padding-left:415px}.trending-interests .interests .topics{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0 7px}.trending-interests .interests .topics:nth-child(2){display:none}.trending-interests .interests .topics:last-child{display:none}.trending-interests .interests .see-more{display:none}}

.left-portion{overflow:scroll;overflow-x:hidden;padding-right:10px}.left-portion.dark-mode{background:black}.left-portion.dark-mode::-webkit-scrollbar-thumb{background-color:rgba(255,255,255,0.733);border-radius:20px}.left-portion::-webkit-scrollbar{width:9px;background:rgba(0,0,0,0.1);border-radius:20px}.left-portion::-webkit-scrollbar-thumb{background-color:#1b1717;border-radius:20px}

.sidebar{background:white;display:flex;flex-direction:column;padding:15px;border-radius:20px;box-shadow:0 0 20px 0 rgba(0,0,0,0.1);height:97vh;width:95%}.sidebar.hide-sidebar{transform:translateX(-300px);width:0%;transition:1s all}.sidebar .logo{width:100%;height:50px;display:flex;justify-content:center;align-items:center;margin:0 auto}.sidebar .logo span{font-family:"Noto Sans";letter-spacing:0.1px;font-weight:bold}.sidebar .logo .MuiSvgIcon-root{color:red;font-size:40px}.sidebar .sidebar-headings{text-transform:uppercase;font-size:12px;margin:14px 0 14px 0px;color:rgba(15,15,15,0.9)}.sidebar .navigation-options{height:240px;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-around}.sidebar .navigation-options .option{text-decoration:none;color:rgba(15,15,15,0.8);width:100%;padding:2px;display:flex;align-items:center}.sidebar .navigation-options .option img{width:30px;height:30px;border-radius:50%}.sidebar .navigation-options .option .MuiSvgIcon-root{font-size:20px}.sidebar .navigation-options .option span{line-height:5px;font-size:14px;margin-left:12px}.sidebar .subscriptions-list{min-height:130px;max-height:140px}.sidebar .subscriptions-list .option span{font-size:13px;color:rgba(0,0,0,0.8)}.sidebar .more-from-yt{min-height:100px;max-height:100px}.dark-mode{background:#1f1e1e}.dark-mode .logo span{color:white}.dark-mode .sidebar-headings{color:white}.dark-mode .navigation-options .option{color:white}.dark-mode .subscriptions-list .option span{color:white}

.youtube-home{display:grid;height:100vh;grid-template-columns:16.5% 83.46%;background-color:#f5f5f5;padding:15px;width:100%;padding-right:0;transition:0.2s all}.youtube-home.hide-sidebar{grid-template-columns:0% 100%}.youtube-home.dark-mode{background-color:#000}@media screen and (max-width: 560px){.youtube-home{grid-template-columns:100%;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}}

